import React from 'react';
import styled from 'styled-components';
import { PieChart, Pie, Cell, Tooltip, Legend } from 'recharts';
// import usePageWidth from '../../hooks/usePageWidth';
import useTeam from '../../hooks/useTeam';

const StyledTeamWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
`;

const StyledPie = styled(PieChart)`
  background-color: #fff;
  padding: 2rem 3rem;
  border-radius: 7px;
  box-shadow: 3px 6px 10px ${({ theme }) => theme.cardShadow};
  display: flex;
  align-items: center;
  justify-content: center;
  justify-items: center;
  width: 40rem;
  height: 30rem;
`;

const StyledHeading = styled.p`
  font-size: 2.4rem;
  color: ${({ theme }) => theme.textSecondary};
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
`;

const TeamChart: React.FC = () => {
  // const pageWidth = usePageWidth();
  const team = useTeam();
  const filterRole = (role: string) => team.filter((doc: { user: { type: string } }) => doc.user.type === role);

  // const cardWidth = () => (pageWidth <= 450 ? 300 : 400);
  // const cardHeight = () => (pageWidth <= 450 ? 300 : 300);

  const teamData = [
    { name: 'Developers', value: filterRole('developer').length || 1 },
    { name: 'Project Managers', value: filterRole('project-manager').length || 1 },
    { name: 'Designers', value: filterRole('designer').length || 1 },
    { name: 'QA', value: filterRole('quality-assurance').length || 1 },
  ];

  const COLORS = ['#1FC844', '#F7B801', '#ea4c89', '#2E5BFF'];

  return (
    <StyledTeamWrapper>
      {teamData.length < 1 ? (
        <StyledHeading>Add members</StyledHeading>
      ) : (
        <>
          <StyledHeading>Team</StyledHeading>
          <StyledPie width={300} height={300}>
            <Pie data={teamData} innerRadius={80} outerRadius={110} dataKey="value" label>
              {teamData.map((entry, index) => (
                <Cell fill={COLORS[index % COLORS.length]} />
              ))}
              <Legend />
            </Pie>
            <Tooltip />
          </StyledPie>{' '}
        </>
      )}
    </StyledTeamWrapper>
  );
};

export default TeamChart;
